.components-base-control__field .dem-field__input_text,
.components-base-control__field .dem-field__input_textarea {
  width: 100%; }

.dem-field__value {
  box-sizing: border-box;
  display: inline-block;
  margin: 5px auto;
  padding: 5px;
  border: 1px solid #ddd;
  width: 350px;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.07);
  color: #32373c;
  background-color: #f1f1f1;
  outline: 0;
  transition: outline 50ms ease-in-out, color 50ms ease-in-out, border-color 50ms ease-in-out, box-shadow 50ms ease-in-out, background-color 50ms ease-in-out; }
  .field-icon ~ .dem-field .dem-field__value {
    width: 60px; }
  .components-base-control__field .dem-field__value {
    width: auto;
    max-width: 100%; }
  .dem-field_empty .dem-field__value {
    display: none; }
  .dem-field__value:hover, .dem-field__value:focus {
    background-color: #fff;
    color: #124964;
    border-color: #5b9dd9;
    box-shadow: 0 0 0 1px #5b9dd9, 0 0 2px 1px rgba(30, 140, 190, 0.8);
    outline: 1px solid transparent; }
  .dem-field__value__img {
    display: block;
    max-width: 100%;
    margin: auto; }

.dem-field__actions {
  display: block;
  font-size: 0;
  line-height: 0; }

.dem-field__action {
  display: inline-block;
  margin: 2px;
  font-size: 0;
  line-height: 0;
  text-decoration: none;
  outline: none;
  color: #72777c;
  transition: color 50ms ease-in-out; }
  .dem-field__action:first-child {
    margin-left: 0; }
  .dem-field__action:last-child {
    margin-right: 0; }
  .dem-field__action:hover {
    color: #23282d; }
  .dem-field__action:focus {
    color: #124964;
    box-shadow: none;
    outline: none; }
  .dem-field__action::before {
    border-radius: 50%;
    text-indent: -1px;
    font: normal 20px/1 dashicons;
    speak: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
  .dem-field__action_add::before {
    content: '\f132'; }
  .dem-field_value .dem-field__action_add {
    display: none; }
  .dem-field__action_add-image::before {
    content: '\f128'; }
  .dem-field__action_add-url::before {
    content: '\f103'; }
  .dem-field__action_edit::before {
    content: '\f463'; }
  .dem-field_empty .dem-field__action_edit {
    display: none; }
  .dem-field__action_remove::before {
    content: '\f158'; }
  .dem-field_empty .dem-field__action_remove {
    display: none; }

